export default {
    props: {
        categories: {
            type: Array,
            default: [{
                    cateid: 0,
                    name: "全部"
                },
                {
                    cateid: 1,
                    name: "风景"
                },
                {
                    cateid: 2,
                    name: "游记"
                },
                {
                    cateid: 3,
                    name: "世界"
                }
            ],
        }
    },
    data() {
        return {
            // 当前选择的分类
            cateCurrentIndex: 0,
            // 内容实例，内容区域您根据项目情况设计即可，覆盖模式
            content: "全部"
        };
    },
    methods: {
        gotodemo: function() {
            uni.navigateTo({
                url: '../segmented-control-demo/segmented-control-demo'
            })
        },
        tabChange: function(e) {
            // 选中的索引
            var index = e.currentTarget.dataset.index;
            // 具体的分类id
            var cateid = e.currentTarget.dataset.cateid;
            this.cateCurrentIndex = index;
            // 动态替换内容
            this.$emit('tabChange', {
                cateid,
                index
            })
        }
    }
}
